	export default {
		data() {
			return {
               dataList:[]
			}
		},
		methods:{
			skillDetails(id){
				this.$api.navTo(`/pages/zhiweiDetails?id=${id}`)
			},
			setData(val) {
				val.forEach(item => {
					this.dataList.push(item)
				})
			},
			initData() {
				this.dataList = []
			}
			
		}
	}
